<template>
    <div class="login_warper">
        <div>
            <label for="rootCompanyId">公司编码</label>
            <input class="login_input" type="text" v-model="loginData.rootCompanyId" name="" id="rootCompanyId" />
        </div>
        <div>
            <label for="rootCompanyId">&emsp;用户名</label>
            <input class="login_input" type="text" v-model="loginData.loginName" name="" id="loginName" />
        </div>
        <div>
            <label for="rootCompanyId">&emsp;&emsp;密码</label>
            <input class="login_input" type="password" v-model="loginData.password" name="" id="password" />
            <input type="hidden" name="rememberMe" v-model="loginData.rememberMe" />
        </div>
        <div class="btn_warp">
            <span class="btnlogin" @click.stop="login">面试官登录</span>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import { api, dicCode } from '../../static/dicConfig'
export default {
    data() {
        return {
            loginData: {
                rootCompanyId: null,
                loginName: null,
                password: null,
                rememberMe: 0            },
            // loginData: {
            //     rootCompanyId: '10890',
            //     loginName: 'admin',
            //     password: 'admin111111',
            //     rememberMe: 0
            // }
        }
    },
    methods: {
        login() {
            let self = this;
            // debugger
            // let postData = new FormData();
            // postData.append("rootCompanyId", self.rootCompanyId);
            // postData.append("loginName", self.loginName);
            // postData.append("password", self.password);
            // postData.append("rememberMe", self.rememberMe);

            axios.post(api.svip + "user/login", self.loginData)
                .then((result) => {
                    if (result.status === 200) {
                        console.log(result);
                        if (result.data) {
                            if (result.data.returncode === 0) {
                                self.$router.push({ name: "ExaminationRoom" });
                            }
                            else {
                                mui.toast(result.data.message || '登录失败！请更正用户信息并重试。');
                            }
                        }
                    }
                }).catch((r) => { console.log(r); })

        }
    }
}
</script>

 <style lang="less" scoped>
@import "../assets/css/login.less";
.login_warper {
    text-align: center;
  margin: 320/@r auto;
  width: 80%;
  font-size: 28/@r;
  .login_input {
    border-bottom: solid #cecece 1px;
  }
  .btn_warp {
    margin-top: 50/@r;
      text-align: center;
    .btnlogin {
      
      border: solid #000000 1px;
    }
  }
}
</style>



